* {
  font-family: Lato;
}

.header {
  text-align: center;
  align-items: center;
}

.headerTitles {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  color: white;
}

.headerTitleSm {
  margin-top: 4%;
  position: absolute;
  color: white;
  top: 10%;
  font-size: 5vw;
  text-shadow: 4px 4px 10px rgba(0, 0, 0, 0.3);
  font-weight: bold;
}

.headerTitleLg {
  margin-top: 30vh;
  position: absolute;
  top: 15%;
  font-size: 7vw;
  font-weight: bold;
  color: #ffc106;
  text-shadow: 4px 4px 10px rgba(0, 0, 0, 0.3);
}

.headerImg {
  width: 100%;
}

.bodyP {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 1%;
  color: white;
  font-size: 1vw;
}

.bodyI {
  width: 40vw;
  margin-top: 0.3%;
}

.fade-in {
  opacity: 0;
  transition: opacity 500ms ease-in;
}

.fade-in.appear {
  opacity: 1;
}
